import React from 'react';
import {Box, Grid, Typography} from "@mui/material";
import {
    greyBackground,
    sxAchievementsMainOuterBox, sxCard, sxName,
    sxOuterBackgroundBox,
    sxRibbon, sxHexaImage, sxText, sxTopBanner, textColorGrey, textColorYellow, yellowBackground,
    sxHexaInternalBox, sxHexaMostInternalBox, styleAwardLogo, titleBanner
} from "./stylesheets/AchievementStyles";
import award1 from './assets/imgs/award1.jpg';
import award2 from './assets/imgs/award2.png';
import award3 from './assets/imgs/award3.png';

const Achievement = () => {

    const data = [
        {
            title: "Awarded Silver for Work Proficiency",
            img: award1,
        },
        {
            title: "E-Commerce Product Development",
            img: award2,
        },
        {
            title: "Gold Medal in Software Engineering",
            img: award3,
        }
    ]
    
    return(<Box id={'achievements'}>
        <Box sx={sxAchievementsMainOuterBox}>
            <Box sx={sxRibbon}>
                <Box sx={{backgroundColor: '#41434B', position: 'relative', left: '80%', width: '20%', height: '100%'}}/>
            </Box>
            <Box sx={sxOuterBackgroundBox}>
                <Box sx={{height: '100%'}}>
                    <Grid container sx={{height: '100%'}}>
                        <Grid item xs={12} sx={sxTopBanner}>
                            <Typography variant={'h2'} sx={titleBanner}>Achievements</Typography>
                        </Grid>
                        <Grid item xs={12} sx={{display: 'flex', alignItems: 'end', mt: {xs: '0rem', md: '10rem'}}}>
                            <Grid container>
                                {data.map((Items, Index) =>(<Grid item xs={12} sm={6} md={4} sx={{mt: {xs: '10rem', md: '0rem'}}}>
                                    <Box sx={{ ...sxCard, ...(Index % 2 === 0 ? greyBackground : yellowBackground)}}>
                                        <Box sx={{...sxHexaImage, ...(Index % 2 === 0 ? yellowBackground : greyBackground)}}>
                                            <Box sx={{...sxHexaInternalBox, ...(Index % 2 === 0 ? greyBackground : yellowBackground)}}>
                                                <Box sx={{...sxHexaMostInternalBox, ...(Index % 2 === 0 ? yellowBackground : greyBackground)}}>
                                                    <img style={styleAwardLogo} src={Items.img} alt={"Award"+Index} />
                                                </Box>
                                            </Box>
                                        </Box>
                                        <Box sx={sxText}>
                                            <Typography sx={{...sxName, ...(Index % 2 === 0 ? textColorYellow : textColorGrey)}}>{Items.title}</Typography>
                                        </Box>
                                    </Box>
                                </Grid>))}
                            </Grid>
                        </Grid>
                    </Grid>
                </Box>
            </Box>
        </Box>
    </Box>)
}

export default Achievement;