import React, {useState} from 'react';
import {
    appbarStyle,
    nameStyle,
    buttonStyles,
    toolbarStyle,
    logoStyle,
    navbarBoxStyle, buttonActiveStyle, drawerStyles
} from "./stylesheets/navbarStyles";
import {
    AppBar,
    Box, IconButton,
    List,
    ListItemButton,
    ListItemIcon, ListItemText,
    Stack,
    Toolbar,
    Typography,
    useMediaQuery,
    useTheme,
    Drawer
} from "@mui/material";
import logo from './assets/imgs/mj_logo.png';
import {Home, MenuRounded, Comment, Assignment, Code, BusinessCenter} from "@mui/icons-material";
import Event from '@mui/icons-material/EmojiEvents';
import Exp from '@mui/icons-material/QueryStats';

const Navbar = () => {
    const theme = useTheme();
    const isSmallScreen = useMediaQuery(theme.breakpoints.down('md'));

    const [activeButton, setActiveButton] = useState(null);
    const [open, setOpen] = useState(false);
    const scrollToSection = (id) => {
        const element = document.getElementById(id);
        if (element) {
            element.scrollIntoView({ behavior: 'smooth' });
        }
    };

    const data = [
        {
            icon: <Home sx={drawerStyles.tabIconStyle}/>,
            title: "Home",
            data: "home",
        },

        {
            icon: <Comment sx={drawerStyles.tabIconStyle}/>,
            title: "About",
            data: "about",
        },

        {
            icon: <Event sx={drawerStyles.tabIconStyle}/>,
            title: "Achievements",
            data: "achievements",
        },

        {
            icon: <Assignment sx={drawerStyles.tabIconStyle}/>,
            title: "Projects",
            data: "projects",
        },

        {
            icon: <Exp sx={drawerStyles.tabIconStyle}/>,
            title: "Experience",
            data: 'experience',
        },

        {
            icon: <Code sx={drawerStyles.tabIconStyle}/>,
            title: "Skills",
            data: "skills",
        },

        {
            icon: <BusinessCenter sx={drawerStyles.tabIconStyle}/>,
            title: "Work",
            data: 'work'
        },
    ]

    const handleButtonClick = (id, index) => {
        if(isSmallScreen)
            setOpen(false);
        scrollToSection(id);
        setActiveButton(index); // Set the active button when clicked
    };

    return (
        <div>
            <AppBar position="fixed" sx={appbarStyle}>
                <Toolbar sx={toolbarStyle}>
                    <Stack direction={'row'} columnGap={2} justifyContent={'center'} alignItems={'center'}>
                        <img src={logo} alt={'logo'} style={logoStyle}/>
                        <Typography variant={'h5'} sx={nameStyle}>
                            Mumshad Jawad
                        </Typography>
                    </Stack>
                    <Box sx={navbarBoxStyle}>
                        {!isSmallScreen ?
                            (data.map((Item, Index) =>(<Box sx={
                            (activeButton === Index ? buttonActiveStyle : buttonStyles)
                        } onClick={()=> handleButtonClick(Item.data, Index)}>
                            <Typography variant={'subtitle1'}>{Item.title}</Typography>
                        </Box>))) :
                            <Box>
                                <Drawer PaperProps={{sx: drawerStyles.drawerSX}} anchor={'top'} open={open} onClose={() => setOpen(false)}>
                                    <List>
                                        {data.map((Item, Index) =>(<ListItemButton sx={{width: '100%'}} onClick={() => handleButtonClick(Item.data, Index)}>
                                            <ListItemIcon sx={{width: '100%'}}>
                                                {Item.icon}
                                                <ListItemText sx={drawerStyles.listStyle}>{Item.title}</ListItemText>
                                            </ListItemIcon>
                                        </ListItemButton>))}
                                    </List>
                                </Drawer>

                                <IconButton sx={{color: '#262930'}} onClick={() => setOpen(!open)}>
                                    <MenuRounded />
                                </IconButton>
                            </Box>
                        }
                    </Box>
                </Toolbar>
            </AppBar>
        </div>
    );
};

export default Navbar;